*{
    padding: 0;
    margin: 0;
    
    
}
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@700&display=swap');

body{
    font-family: 'Roboto', sans-serif;
}


.app{
    align-items: center;
    justify-content: center;
    background-color: #820000;
    width: 100%;
    min-height: 100vh;
    display: flex;
}
input{
    box-shadow: inset #abacaf 0 0 0 2px;
    border: 0;
    background: #283739;
    border-radius: 3px;
    padding: 9px 12px;
    line-height: 1.4;
    color: rgb(0, 0, 0);
    font-size: 16px;
    font-weight: 400;
    height: 30px;
}
input:hover{
    box-shadow: 0 0 0 0 #fff inset, #1de9b6 0 0 0 2px;
}
input:focus{
    background: #fff;
    outline: 0;
    box-shadow: 0 0 0 0 #fff inset, #1de9b6 0 0 0 3px;
}

.form11{
    background-color: teal;
    padding: 35px;
    border-radius: 10px;


}
button {
    background-color: #fff000;
    border-radius: 12px;
    color: #000;
    cursor: pointer;
    font-weight: bold;
    padding: 10px 15px;
    text-align: center;
    width: 100%;
    box-sizing: border-box;
    border: 0;
    font-size: 16px;
    user-select: none;
    
}
button:not(:disabled):hover,button:not(:disabled):focus {
    outline: 0;
    background: #f4e603;
    box-shadow: 0 0 0 2px rgba(0,0,0,.2), 0 3px 8px 0 rgba(0,0,0,.15);
}
h1{
    color :white;
    font-size: 40px;
}
  
 